#governance {
  .window {
    text-align: center;
    padding: 25px 16px 22px 16px;
  }

  .my-vote {
    padding: 25px 0px 22px 0px;
  }

  .vote-info {
    padding: 25px 24px 22px 24px;
  }

  .mining-weight {
    padding: 30px 0 0 0;
  }

  input {
    border: none;
  }

  .card-title {
    color: #333;
    font-size: 18px;
    line-height: 28px;
    font-weight: 500;
    position: relative;

    .anticon {
      width: 16px;
      height: 16px;
      line-height: 16px;
      vertical-align: initial;
      margin-left: 4px;
    }
  }

  .record-table {
    .ant-table-pagination.ant-pagination {
      margin-bottom: 10px;

      .anticon {
        display: inline-block;
        width: 6px;
        height: 9px;

        svg {
          display: none;
        }
      }

      .ant-pagination-prev {
        .anticon {
          background: url(../images/left-black.svg) -0.5px 1px no-repeat;
          background-size: 100%;
        }

        &:hover {
          .anticon {
            background: url(../images/left-hover.svg) -0.5px 1px no-repeat;
            background-size: 100%;
          }
        }

        &.ant-pagination-disabled {
          .anticon {
            background: url(../images/left-gray.svg) -0.5px 1px no-repeat;
            background-size: 100%;
          }
        }
      }

      .ant-pagination-next {
        .anticon {
          background: url(../images/right-black.svg) 0.5px 0 no-repeat;
          background-size: 100%;
        }

        &:hover {
          .anticon {
            background: url(../images/right-hover.svg) 0.5px 0 no-repeat;
            background-size: 100%;
          }
        }

        &.ant-pagination-disabled {
          .anticon {
            background: url(../images/right-gray.svg) 0.5px 0 no-repeat;
            background-size: 100%;
          }
        }
      }
    }

    button.ant-pagination-item-link {
      min-width: 22px;
      padding: 0 0;
      height: 22px;

      .anticon {
        margin-left: 0;
      }
    }

    .ant-pagination-item-active {
      border: none;

      a {
        color: #333;
      }
    }

    ul li {
      width: 22px;
      height: 22px;
      line-height: 22px;
      border: none;

      &.ant-pagination-item {
        width: 25px;
      }

      a {
        color: #999;
        font-size: 12px;
      }

      button {
        border: 1px solid #e6e9eb !important;
        background: transparent !important;
        display: inline-flex;
        justify-content: center;
        align-items: center;

        &:hover {
          border: 1px solid #5915e1 !important;
        }
      }

      &.ant-pagination-disabled button:disabled:hover {
        border: 1px solid #e6e9eb !important;
      }
    }

    .ant-pagination-prev {
      margin-right: 10px;
    }

    .ant-pagination-next {
      margin-left: 10px;
    }

    .ant-select .ant-select-selector {
      width: 80px;
      height: 22px;
      .ant-select-selection-item {
        width: 50px;
        height: 22px;
        line-height: 22px;
      }
    }
  }

  .my-vote .card-title {
    margin-bottom: 39px;
  }

  .ant-select {
    width: 100%;
    background: #fff;

    &.ant-select-disabled {
      background-color: #f0f0f0;
    }

    .ant-select-selector {
      width: 180px;
      height: 36px;
      line-height: 36px;
      border: 1px solid #e1e1e1;
      border-radius: 5px;
      background: transparent;
      box-shadow: 0 0 red;
    }
  }

  .ant-select-open {
    .ant-select-selector {
      border: 1px solid #5915e1;
    }
  }

  .ant-input-affix-wrapper.ant-input-affix-wrapper-focused {
    border: 1px solid #5915e1;
  }

  .ant-select-selection-placeholder {
    font-size: 12px;
    line-height: 36px;
    color: #333333;
    text-align: left;
  }

  .ant-form-item-label {
    margin-right: 6px;
    overflow-wrap: break-word;
    white-space: normal;
    text-align: left;
    overflow: auto;
    max-width: 130px;
    height: 40px;
    padding: 0;
    overflow: hidden;

    &::-webkit-scrollbar {
      display: none;
    }
  }

  #control-hooks .ant-form-item-control {
    width: 180px;
  }

  .ant-select-selection-item {
    width: 180px;
    height: 36px;
    line-height: 36px;
    font-size: 12px;
  }

  .ant-form-inline {
    justify-content: space-between;
  }

  .ant-input-affix-wrapper {
    border: 1px solid #e1e1e1;
    border-radius: 5px;
    box-shadow: 0px 0px red;
    height: 36px;
  }

  #control-hooks_votePercent {
    height: 34px;
    font-size: 12px;
    color: #333333;
  }

  .ant-table-content::-webkit-scrollbar {
    display: none;
  }

  .ant-form-item {
    padding: 0 24px;
    margin: 0;

    &:nth-child(-n + 2) {
      // height: 36px;
    }

    &:nth-child(3) {
      width: 100%;
      margin-top: 30px;
    }
  }

  .vote-info {
    font-size: 12px;
    line-height: 17px;
    font-weight: 400;
    color: #333333;
    margin-bottom: 10px;

    span {
      display: inline-block;
      margin-right: 16px;
    }

    i {
      font-size: 14px;
      font-weight: 500;
      font-style: normal;
    }
  }

  .vote-btn {
    width: 316px;
    height: 40px;
    font-weight: 500;
    font-size: 14px;
  }

  .vote-table {
    padding: 0 16px;
  }

  .ant-form-item-has-error .ant-select:not(.ant-select-disabled):not(.ant-select-customize-input) .ant-select-selector {
    border-color: #e1e1e1 !important;
  }

  .ant-table {
    .ant-table-thead {
      border-bottom: 1px #e5e5e5 solid;

      tr > th {
        font-size: 12px;
        color: #999999;
        line-height: 20px;
        text-align: right;

        &:first-child {
          text-align: left;
          width: 37%;
        }

        &:last-child {
          padding-right: 5px;
        }

        &::before {
          background: none;
        }
      }
    }

    .ant-table-tbody {
      margin-top: 8px;

      tr > td {
        text-align: right;

        &:first-child {
          text-align: left;
          width: 37%;
        }
      }
    }
  }

  .pool-apy-table .anticon {
    margin-left: 3px;

    svg {
      width: 12px;
      height: 12px;
    }
  }

  .vote-info {
    .card-title {
      margin-bottom: 25px;
    }

    .vote-info-detail {
      display: flex;
      flex-direction: row;
      padding: 0;

      p {
        flex: 1;
        font-size: 14px;
        font-weight: 400;
        color: #999999;
        text-align: left;
        // white-space: nowrap;
        width: 50%;

        &:nth-child(2) {
          margin-left: 80px;
        }
      }

      span:nth-child(1) {
        margin-right: 8px;
      }

      span:nth-child(2) {
        color: #333333;
        word-break: break-all;
      }
    }
  }

  .ant-table-cell button {
    font-weight: 400;
    height: 24px;
    color: #5915e1;
    background: no-repeat;
    border: 1px #5915e1 solid;
    line-height: 24px;
    font-size: 12px;
    padding: 0;
    width: 50px;
    min-width: 50px;

    &:disabled {
      color: #ccc;
      border-color: #ccc;
      background: #f5f5f5 !important;
    }
  }

  .ant-form-item-explain-error {
    color: #333333;
    margin-top: 5px;
    background: #f8f7c8;
    line-height: 20px;
    border-radius: 5px;
    font-size: 12px;
    text-align: left;
    padding: 6px 10px;
    max-width: 180px;
    word-break: break-all;

    div[role] {
      word-break: break-word;
    }
  }
}

.pool-selector {
  width: 100%;
}

.pool-selector-new {
  width: 100% !important;

  .ant-select-selector {
    width: 100% !important;
  }
}

.lastest-vote-chart {
  height: 270px;
}

.toLockSun {
  color: #5915e1;
  font-size: 12px;
  position: absolute;
  right: 24px;
  font-weight: 400;
  top: 0;
  bottom: 0;
  margin: auto;
  height: 17px;
  line-height: 17px;
}

.countDown {
  font-size: 12px;
  line-height: 17px;
  margin-top: 3px;

  span {
    letter-spacing: 1px;
  }
}

.vote-table {
  margin-top: 20px;
}

.ant-tooltip-inner {
  width: 184px;
  padding: 14px 16px;
  font-size: 12px;
  border-radius: 10px;
  background: rgba(27, 31, 38, 0.9);
}

.ant-select-dropdown.gover-dropdown-new {
  background: #fff !important;
  width: 300px !important;
  .ant-select-item {
    color: #111 !important;
    background: #fff !important;
    &.ant-select-item-option-active:not(.ant-select-item-option-disabled) {
      background: rgba(89, 21, 225, 0.15) !important;
    }
  }
}

div.combine-icon img.token-logos {
  display: inline-block !important;
  height: 20px !important;
  width: auto !important;
  vertical-align: middle !important;
  margin-right: 4px !important;
  margin-left: 0 !important;
}

.combine-icon {
  position: relative;
  display: inline-block;
  // width: 50px;
  text-align: left;
  font-size: 14px;
  height: 20px;
  line-height: 15px;
  margin-right: 1px;
  vertical-align: text-top;

  img {
    margin-left: -10px;

    &:first-child {
      margin-left: 0px;
    }

    &.pool2-img {
      height: 1.08em;
      width: 2em;
      margin-right: 4px;
    }
  }
}

#governance .ant-table.ant-table-empty .ant-table-tbody tr > td:first-child {
  text-align: center;
}

.ant-select-dropdown.gover-dropdown-new .ant-select-item {
  font-size: 13px !important;
  &:hover {
    color: #111 !important;
    background: rgba(89, 21, 225, 0.15) !important;
  }
  &.ant-select-item-option-selected {
    color: #111 !important;
  }
}

@media screen and (max-width: 800px) {
  #governance {
    .ant-select .ant-select-selector {
      width: 100%;
    }

    .ant-form-item:nth-child(3) {
      margin-top: 10px;
    }

    .ant-form-item-label {
      max-width: inherit;
    }

    .ant-form-item:nth-child(2) {
      margin-top: 10px;
    }

    .vote-btn {
      width: 100%;
    }

    .ant-form-item-explain-error {
      max-width: initial;
    }

    .vote-info {
      .vote-info-detail {
        display: block;

        p {
          white-space: normal;
          line-height: 30px;
          width: 100%;

          &:nth-child(2) {
            margin-left: 0;
          }
        }
      }
    }

    .my-vote {
      .ant-table .ant-table-thead tr > th,
      .ant-table .ant-table-tbody tr > td {
        width: 30%;
      }
    }

    .vote-record {
      .ant-table .ant-table-thead tr > th,
      .ant-table .ant-table-tbody tr > td {
        &:nth-child(1) {
          width: 35%;
        }

        &:nth-child(2),
        &:nth-child(4) {
          width: 20%;
        }
      }
    }

    .toLockSun {
      top: 60px;
      left: 0;
      right: 0;
    }
  }

  #governance .vote-info .card-title {
    margin-bottom: 15px;
  }
}
